<script>
export default {
  data() {
    return {
      search: "",
      gift: "",
      sortBy: "",
      keys: ["All", "Active", "Sespended", "Secession"],
      headers: [
        { text: "Code", align: "start", value: "code" },
        { text: "Name", value: "name" },
        { text: "Email", value: "email" },
        { text: "Phone", value: "phone" },
        { text: "Country", value: "country" },
        { text: "Member level", value: "level" },
        { text: "Join date", value: "join" },
        { text: "Credit Gift", value: "checkbox" },
      ],
      dialog: false,
    };
  },

  methods: {
    resetInput() {
      let inputCheckbox = document.querySelectorAll(
        ".table_head .v-input--selection-controls__input input"
      );

      inputCheckbox.forEach((element) => {
        if (element.checked) {
          element.click();
        }
      });

      this.search = "";
      this.sortBy = "";
    },
    showDialog() {
      this.dialog = true;
    },
    closeDialog() {
      this.dialog = false;
    },
  },
};
</script>
